
import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';

import 'sweetalert2/src/sweetalert2.scss';
import Swal from 'sweetalert2/dist/sweetalert2.js';
import { ManagerAdd, ManagerRe } from 'src/app/demo/_model/_Manager/manager';
import { ManagerService } from 'src/app/demo/Services/manager.service';

import { ToastrService } from 'ngx-toastr';
import { UserPermissions } from 'src/app/demo/_model/userPermissions';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
@Injectable({
  providedIn: 'root'
})
export class SharedDeletedManagerService {
  //prop
  selected: ManagerRe = { id: 0 } as ManagerRe;

  TimeArray = [];
  PermissionAray = [];
  managerTeachersArray = [];
  managerStudentsArray = [];
  StudentsTeachersArray = [];
  circleManagersArray = [];
  IsNew: Boolean = true;
  public btnLoader: boolean;
  public submitLoader: boolean;
  managers: ManagerRe[];
  //form
  form: FormGroup;
  IsLoaded: boolean = false;
  managersService: BaseService<ManagerRe>;

  constructor(
    private formBuilder: FormBuilder,
    private AccountService: AccountControllerService,
    private AccountsService: AccountService,
    private managerService: ManagerService,
    private toastr: ToastrService,
    private httpSv: HttpService

  ) { this.managersService = new BaseService<ManagerRe>(httpSv, ApiPath.Manager); }

  restore(id:number){
    this.managersService.restore(id).subscribe(res=>{
      location.reload();
    });
  }
 
  GetData() {
      this.managersService.GetDeleted().subscribe((res) => {
        this.managers = res;
        this.IsLoaded = true;
      });
  }
}
