import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';


import { CircleRe } from 'src/app/demo/_model/_Circle/circle';
import { SharedCircleService } from './shared/shared-circle.service';

import { AccountService } from 'src/app/demo/Services/account/account.service';
import { StudentReportRe } from 'src/app/demo/_model/StudentReport';
import { Router } from '@angular/router';


@Component({
  selector: 'app-circle',
  templateUrl: './circle.component.html',
  styleUrls: ['./circle.component.scss']
})
export class CircleComponent implements OnInit {
  RegisterForm: FormGroup;
  circles: CircleRe[];
  circle: CircleRe;
  addCircleButton :string = "اضافة حلقة"
  deleteCircleButton :string = "ازالة حلقة"
  circleService: BaseService<CircleRe>;
  dtExportButtonOptions?: any = {};
  IsLoaded :boolean = false;
  constructor(
    public shared: SharedCircleService,
    private formBuilder: FormBuilder,
    private AccountService: AccountService,
    private httpSv: HttpService,
    private router :Router
  ) {
    if (!AccountService.user) {
      this.router.navigateByUrl('/auth/signin-v2');
    }
    this.circleService = new BaseService<CircleRe>(httpSv, ApiPath.Circle);

  }

  ngOnInit() {

    this.shared.initForm();
    this.shared.initReportForm();
    if (this.AccountService.user.appUserType.name_en == "Admin") {
      this.circleService.GetAll().subscribe((res) => {
        this.circles = res;
        this.IsLoaded = true;

      });
    } if (this.AccountService.user.appUserType.name_en == "Manager") {
      this.circleService.GetByManagerId().subscribe((res) => {
        this.circles = res;
        this.IsLoaded = true;

      });
    } if (this.AccountService.user.appUserType.name_en == "Teacher") {
      this.circleService.GetByTeacherId().subscribe((res) => {
        this.circles = res;
        this.IsLoaded = true;

      });
    }
    
    this.dtExportButtonOptions = {
      dom: 'Bfrtip',
      buttons: ['copy','excel','print']
    };
  }
  addReport(report: StudentReportRe, model: any) {
    this.shared.initReportForm(report);
    // this.shared.IsNew = true;
    model.show();
  }
  add(circle: CircleRe, model: any) {
    this.shared.initForm(circle);
    this.shared.IsNew = true;
    model.show();
  }
  edit(circle: CircleRe, model: any) {
    this.shared.initForm(circle);
    this.shared.IsNew = false;
    model.show();
  }
  getCircleDetails(circle: any, modal: any) {
    this.circle = circle;
    console.log(circle);
    console.log("circle");
    modal.show();
  }
  close(modal: any) {
    this.shared.clear()
    modal.hide();
  }
}


