import { Injectable } from '@angular/core';
import { HttpService } from '../http/http.service';

@Injectable({
  providedIn: 'root'
})
export class CircleService {
  private controller: string = 'Circle/';
  constructor(private httpservice: HttpService) {
  }

  GetAll() {
   return this.httpservice.httpGet(this.controller);
  }

  GetById(id: number) {
   return this.httpservice.httpGet(this.controller + 'id/' + id);
  }

  Add(model: any) {
   return this.httpservice.httpPost(this.controller + 'add/', model);
  }

  Update(model: any) {
   return this.httpservice.HttpPost(this.controller + 'update/', model);
  }

  Delete(id: number) {
   return this.httpservice.httpDelete(this.controller  + id);
  }

  restore(id: any) {
    return this.httpservice.HttpPost(this.controller  +  'restore/' + id);
  }

  GetDeletedCircles() {
    return this.httpservice.httpGet(this.controller  + 'GetDeletedCircles');
  }

}
