import { TimeRe } from 'src/app/demo/_model/timeRe';

import { SubscribeAdd, SubscribeRe } from '../../../../_model/subscribe';
import { PayStatue } from '../../../../_model/payStatue';
import { SubscribeType } from 'src/app/demo/_model/subscribeType';
import { FamilyRe } from '../../../../_model/family';
import { Component, Input, OnInit, ViewEncapsulation, Output, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { IOption } from 'ng-select';
import { SnotifyService } from 'ng-snotify';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { GovernorateReDto } from 'src/app/demo/_model/governorate';
import { Nationality } from 'src/app/demo/_model/nationality';

import { SharedManagerService } from '../shared/shared-manager.service';

import { StudentService } from 'src/app/demo/Services/student.service';
import { ManagerStudentRe } from 'src/app/demo/_model/_Manager/managerStudent';
import { ManagerRe } from 'src/app/demo/_model/_Manager/manager';
import { TeacherRe } from 'src/app/demo/_model/_Teacher/teacher';
import { StudentAdd, StudentRe } from 'src/app/demo/_model/_Student/student';
import { CircleRe } from 'src/app/demo/_model/_Circle/circle';

@Component({
  selector: 'app-addManager',
  templateUrl: './addManager.component.html',
  styleUrls: ['./addManager.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class AddManagerComponent implements OnInit {
  permessionRole: string = "الصلاحيات"
  Nationalities: Array<IOption>;
  Governorates: Array<IOption>;
  Familys: Array<IOption>;
  SubscribeTypes: Array<IOption>;
  Subscribes: Array<IOption>;
  PayStatues: Array<IOption>;
  Circles: Array<IOption>;
  Teachers: Array<IOption>;
  Students: Array<IOption>;
  Managers: Array<IOption>;
  Times: Array<IOption>;
  permissions: Array<IOption>;
  NationalityService: BaseService<Nationality>;
  GovernorateService: BaseService<GovernorateReDto>;
  FamilyService: BaseService<FamilyRe>;
  SubscribeTypesService: BaseService<SubscribeType>;
  PayStatueService: BaseService<PayStatue>;
  subscribeService: BaseService<SubscribeAdd>;
  circleService: BaseService<CircleRe>;
  teacherService: BaseService<TeacherRe>;
  managerService: BaseService<ManagerRe>;
  studentsService: BaseService<StudentRe>;

  timesService: BaseService<TimeRe>;
  constructor(

    public shared: SharedManagerService,
    private formBuilder: FormBuilder,
    private AccountService: AccountControllerService,

    private httpSv: HttpService,
    private studentService: StudentService

  ) {
    this.NationalityService = new BaseService<Nationality>(httpSv, ApiPath.Nationality);
    this.GovernorateService = new BaseService<GovernorateReDto>(httpSv, ApiPath.Governorate);
    this.FamilyService = new BaseService<FamilyRe>(httpSv, ApiPath.Family);
    this.SubscribeTypesService = new BaseService<SubscribeType>(httpSv, ApiPath.SubscribeType);
    this.PayStatueService = new BaseService<PayStatue>(httpSv, ApiPath.PayStatue);
    this.subscribeService = new BaseService<SubscribeAdd>(httpSv, ApiPath.Subscribe);
    this.circleService = new BaseService<CircleRe>(httpSv, ApiPath.Circle);
    this.teacherService = new BaseService<TeacherRe>(httpSv, ApiPath.Teacher);
    this.managerService = new BaseService<ManagerRe>(httpSv, ApiPath.Manager);
    this.timesService = new BaseService<TimeRe>(httpSv, ApiPath.Time);
    this.studentsService = new BaseService<StudentRe>(httpSv, ApiPath.Student);
  }
  ngOnInit() {
    this.AccountService.GetPermissions().subscribe((res) => {
      const data = res.map((x) => {
        const temp = {
          label: x.name,
          value: x.id.toString()
        };
        return temp;
      });
      this.permissions = data;


    });


    this.circleService.GetAll().subscribe((res: CircleRe[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.name,
          value: x.id.toString()
        };
        return temp;
      });
      this.Circles = data;

    });
    this.teacherService.GetAll().subscribe((res: TeacherRe[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.firstName + ' ' + x.lastName,
          value: x.id.toString()
        };
        return temp;
      });
      this.Teachers = data;
    });
    this.managerService.GetAll().subscribe((res: ManagerRe[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.firstName + ' ' + x.lastName,
          value: x.id.toString()
        };
        return temp;
      });
      this.Managers = data;

    });
    this.studentService.GetAllForOther().subscribe((res: StudentRe[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.firstName + ' ' + x.lastName,
          value: x.id.toString()
        };
        return temp;
      });
      this.Students = data;


    });
    this.timesService.GetAll().subscribe((res: TimeRe[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.dayName,
          value: x.id.toString()
        };
        return temp;
      });
      this.Times = data;

    });
    this.GovernorateService.GetAll().subscribe((res: GovernorateReDto[]) => {
      const data = res.map((x) => {
        const temp = {
          label: x.name_ar,
          value: x.id.toString()
        };
        return temp;
      });
      this.Governorates = data;

    });
    this.shared.initForm(null);
  }


}
