import { Injectable } from '@angular/core';
import { HttpService } from './http/http.service';

@Injectable({
  providedIn: 'root'
})
export class StudentService {
  controller = "Student/";

  constructor(private http:HttpService) { }
  GetAllForOther() {
    return this.http.httpGet(this.controller  + 'GetStudentsForOther');
  }

  GetDeletedStudents() {
    return this.http.httpGet(this.controller  + 'GetDeletedStudents');
  }

  GetStudentsForCircles() {
    return this.http.httpGet(this.controller  + 'GetStudentsForCircles');
  }
  
  Update(model: any) {
    return this.http.httpPost(this.controller  + 'update/', model);
  }
  Delete(id: any) {
    return this.http.httpDelete(this.controller  + id);
  }


restore(id: any) {
  return this.http.httpPost(this.controller  +  'restore/' + id);
}
  GetByManagerId() {
    return this.http.httpGet(this.controller + 'GetByManagerId');
  }
  GetByTeacherId() {
    return this.http.httpGet(this.controller + 'GetByTeacherId');
  }

  GetByStudentId() {
    return this.http.httpGet(this.controller + 'GetByStudentId');
  }
  UpdatePayStatue(id: number,studentId :number) {
    return this.http.httpPost(this.controller + 'UpdatePayStatue/' + id + "/" + studentId);
  }


  AcceptStudent(studentId :number) {
    return this.http.httpPost(this.controller + 'AcceptStudent/' +  studentId);
  }
  GetPendingByManagerId() {
    return this.http.httpGet(this.controller + 'GetPendingByManagerId');
  }
  GetPendingByTeacherId() {
    return this.http.httpGet(this.controller + 'GetPendingByTeacherId');
  }

  GetPendingByAdminId() {
    return this.http.httpGet(this.controller + 'GetPendingByAdminId');
  }

  GetRequestByManagerId() {
    return this.http.httpGet(this.controller + 'GetRequestByManagerId');
  }
  GetRequestByTeacherId() {
    return this.http.httpGet(this.controller + 'GetRequestByTeacherId');
  }

  GetRequestByAdminId() {
    return this.http.httpGet(this.controller + 'GetRequestByAdminId');
  }



}
