
import { Injectable } from '@angular/core';
import { AbstractControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';

import 'sweetalert2/src/sweetalert2.scss';
import Swal from 'sweetalert2/dist/sweetalert2.js';
import { ManagerAdd, ManagerRe } from 'src/app/demo/_model/_Manager/manager';
import { ManagerService } from 'src/app/demo/Services/manager.service';

import { ToastrService } from 'ngx-toastr';
import { UserPermissions } from 'src/app/demo/_model/userPermissions';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
@Injectable({
  providedIn: 'root'
})
export class SharedManagerService {
  //prop
  selected: ManagerRe = { id: 0 } as ManagerRe;

  TimeArray = [];
  PermissionAray = [];
  managerTeachersArray = [];
  managerStudentsArray = [];
  StudentsTeachersArray = [];
  circleManagersArray = [];
  IsNew: Boolean = true;
  public btnLoader: boolean;
  public submitLoader: boolean;
  managers: ManagerRe[];
  //form
  form: FormGroup;
  IsLoaded: boolean = false;
  managersService: BaseService<ManagerRe>;

  constructor(
    private formBuilder: FormBuilder,
    private AccountService: AccountControllerService,
    private AccountsService: AccountService,
    private managerService: ManagerService,
    private toastr: ToastrService,
    private httpSv: HttpService

  ) { this.managersService = new BaseService<ManagerRe>(httpSv, ApiPath.Manager); }

  // init form
    initForm(data: ManagerRe = null) {
    this.selected = data;
    this.form = this.formBuilder.group({
      id: [data?.id | 0],
      firstName: [data?.firstName, Validators.required],
      lastName: [data?.lastName, Validators.required],
      email: [data?.email, Validators.required],
      governorateId: [data?.governorate?.id.toString(), Validators.required],
      mobileNumber: [data?.mobileNumber, Validators.required],
      managerTimes: [data?.managerTimes],
      managerTeachers: [data?.managerTeachers],
      managerStudents: [data?.managerStudents],
      userPermissions: [data?.userPermissions],
      circleManagers: [data?.circleManagers],
      password: ['', Validators.required, Validators.minLength(6), Validators.maxLength(40)],
      confirmPassword: ['', Validators.required],
    });

    data?.managerTimes?.forEach((e) => {
      this.TimeArray.push(e?.id.toString());
    });
    this.form.get('managerTimes').setValue(this.TimeArray);

    data?.userPermissions?.forEach((e) => {
      this.PermissionAray.push(e.toString());

    });
    this.form.get('userPermissions').setValue(this.PermissionAray);



    data?.circleManagers?.forEach((e) => {
      this.circleManagersArray.push(e?.id.toString());
    });
    this.form.get('circleManagers').setValue(this.circleManagersArray);



    data?.managerTeachers?.forEach((e) => {
      this.managerTeachersArray.push(e?.id.toString());
    });
    this.form.get('managerTeachers').setValue(this.managerTeachersArray);




    data?.managerStudents?.forEach((e) => {
      this.managerStudentsArray.push(e?.id.toString());
    });
    this.form.get('managerStudents').setValue(this.managerStudentsArray);


  }
  get f(): { [key: string]: AbstractControl } {
    return this.form.controls;
  }

  add(modal: any) {
    this.btnLoader = true;
    if (this.form.valid) {
      let data: ManagerAdd = this.form.value;
      data.managerTimes = [];
      data.managerTeachers = [];
      data.managerStudents = [];
      data.circleManagers = [];
      this.form.controls?.managerTimes?.value.forEach((e) => {
        let time = {
          id: 0,
          timeId: e,
          managerId: 0,
          isDeleted: false
        };
        data.managerTimes.push(time);
      });

      this.form.controls?.managerTeachers?.value.forEach((e) => {
        let teachers = {
          id: 0,
          teacherId: e,
          managerId: 0,
          isDeleted: false
        };
        data.managerTeachers.push(teachers);
      });
      this.form.controls?.managerStudents?.value.forEach((e) => {
        let managerStudents = {
          id: 0,
          studentId: e,
          managerId: 0,
          isDeleted: false
        };
        data.managerStudents?.push(managerStudents);
      });
      this.form.controls?.circleManagers?.value.forEach((e) => {
        let circleManagers = {
          id: 0,
          circleId: e,
          managerId: 0,
          isDeleted: false

        };
        data.circleManagers?.push(circleManagers);
      });


      this.AccountService.AddManager(data).subscribe((res) => {

        this.managers.push(res);
        this.form.reset();
        this.btnLoader = false;
        modal.hide();
        this.toastr.success("تم اضافة المشرف بنجاح")
      }),
        (err) => {
          this.toastr.error("تحقق من جودة الاتصال بالانترنت")
          this.btnLoader = false;

        };
    }
  }
  Update(modal: any): void {
    this.btnLoader = true;
    let data: ManagerAdd = this.form.value;
    data.managerTimes = [];
    data.managerTeachers = [];
    data.managerStudents = [];
    data.circleManagers = [];
    this.form.controls?.managerTimes?.value.forEach((e) => {
      let time = {
        id: 0,
        timeId: e,
        managerId: 0,
        isDeleted: false
      };
      data.managerTimes.push(time);
    });

    this.form.controls?.managerTeachers?.value.forEach((e) => {
      let teachers = {
        id: 0,
        teacherId: e,
        managerId: 0,
        isDeleted: false
      };
      data.managerTeachers.push(teachers);
    });
    this.form.controls?.managerStudents?.value.forEach((e) => {
      let managerStudents = {
        id: 0,
        studentId: e,
        managerId: 0,
        isDeleted: false
      };
      data.managerStudents.push(managerStudents);
    });
    this.form.controls?.circleManagers?.value.forEach((e) => {
      let circleManagers = {
        id: 0,
        circleId: e,
        managerId: 0,
        isDeleted: false

      };
      data.circleManagers.push(circleManagers);
    });

    this.managerService.Update(data).subscribe((res) => {
      modal.hide();
      let index = this.managers.findIndex((x) => x.id === data.id);
      this.managers[index] = res;
      this.btnLoader = false;
      this.toastr.success("تم تحديث بيانات المشرف بنجاح")
    },
    (err) => {
      this.toastr.error("تحقق من جودة الاتصال بالانترنت")
      this.btnLoader = false;
    });
  }
  clear() {
    this.selected = { id: 0 } as ManagerRe;
    this.TimeArray = [];
    this.managerTeachersArray = [];
    this.managerStudentsArray = [];
    this.StudentsTeachersArray = [];
  }

  confirmAlert(manager: any) {
    Swal.fire({
      title: 'هل انت متاكد من حذف المشرف',
      text: 'اذا تم الحذف لن تكون قادرا علي الاسترجاع مرة اخرى',
      type: 'warning',
      showCloseButton: true,
      showCancelButton: true
    }).then((willDelete) => {
      if (willDelete.dismiss) {
        Swal.fire('', 'لم يتم الحذف', 'error');
      } else {
        this.managerService.Delete(manager.id).subscribe(res => {

          let index = this.managers.indexOf(manager)
          this.managers.splice(index, 1);
          Swal.fire('', 'تم حذف المشرف', 'success');
          this.toastr.success("تم حذف المشرف بنجاح")
        });
      }
    });
  }

  GetData() {
    if (this.AccountsService.user.appUserType.name_en == "Admin") {
      this.managersService.GetAll().subscribe((res) => {
        this.managers = res;
        console.log(res)
        console.log(this.managers)
        this.IsLoaded = true;

      });
    } else {
      this.managersService.GetByManagerId().subscribe((res) => {
        this.managers = res;
        console.log(this.managers)
        console.log(res)
        this.IsLoaded = true;

      }
      )
    };
  }
}
