import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { SharedFamilyService } from '../family/shared/shared-family.service';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { FamilyRe } from 'src/app/demo/_model/family';


@Component({
  selector: 'app-family',
  templateUrl: './family.component.html',
  styleUrls: ['./family.component.scss']
})
export class FamilyComponent implements OnInit {
  RegisterForm: FormGroup;
  addfamilyButton :string = "اضافة عائلة"
  deletefamilyButton :string = "ازالة عائلة"
  familys: FamilyRe[];
  family: FamilyRe;
  IsLoaded : boolean = false;

  familyService: BaseService<FamilyRe>;
  dtExportButtonOptions?: any = {};

  constructor(
    public shared: SharedFamilyService,
    private formBuilder: FormBuilder,
    private AccountService: AccountService,
    private httpSv: HttpService
  ) {
    this.familyService = new BaseService<FamilyRe>(httpSv, ApiPath.Family);
  }

  ngOnInit() {
    this.dtExportButtonOptions = {
      dom: 'Bfrtip',
      buttons: ['copy','excel','csv','pdf','print']
    };
    this.shared.initForm();
    // if (this.AccountService.user.appUserType.name_en == "Admin") {
    //   this.familyService.GetAll().subscribe((res) => {
    //     this.familys = res;
    //     console.log(this.familys);
    //     console.log(res);
    //   });
    // }if (this.AccountService.user.appUserType.name_en == "Manager") {
    //   this.familyService.GetByManagerId().subscribe((res) => {
    //     this.familys = res;
    //     console.log(this.familys);
    //     console.log(res);
    //   });
    // } else {
    //   this.familyService.GetByfamilyId().subscribe((res) => {
    //     this.familys = res;
    //     console.log(this.familys);
    //     console.log(res);
    //   });
    // }
    this.familyService.GetAll().subscribe((res) => {
      this.familys = res;
      this.IsLoaded = true;
    });
  }

  add(family: FamilyRe, model: any) {
    this.shared.initForm(family);
    this.shared.IsNew = true;
    model.show();
  }
  edit(family: FamilyRe, model: any) {
    this.shared.initForm(family);
    this.shared.IsNew = false;
    model.show();
  }
  getCircleDetails(family: any, modal: any) {
    this.family = family;
    console.log(family);
    console.log("family");
    modal.show();
  }
  close(modal: any) {

    modal.hide();
  }
}


