import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';

import 'sweetalert2/src/sweetalert2.scss';
import Swal from 'sweetalert2/dist/sweetalert2.js';


import { ToastrService } from 'ngx-toastr';
import { StudentAdd, StudentRe } from 'src/app/demo/_model/_Student/student';
import { UserPermissions } from 'src/app/demo/_model/userPermissions';
import { FamilyAdd, FamilyRe } from 'src/app/demo/_model/family';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { PayScreenShotAdd } from 'src/app/demo/_model/payScreenShot';
import { StudentService } from 'src/app/demo/Services/student.service';
import { AccountService } from 'src/app/demo/Services/account/account.service';
@Injectable({
  providedIn: 'root'
})
export class SharedTrashService {
  Students: StudentRe[];
  IsLoaded: boolean = false;

  constructor(private studentSV: StudentService) { }

  GetData() {
    this.studentSV.GetDeletedStudents().subscribe((res) => {
      this.Students = res;
      this.IsLoaded = true;
    });
  }
  restore(id:number){
    this.studentSV.restore(id).subscribe(res=>{
      location.reload();
    })
  }










}
