import { PayStatue } from './../../../_model/payStatue';

import { GovernorateReDto } from '../../../_model/governorate';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { IOption } from 'ng-select';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { Nationality } from 'src/app/demo/_model/nationality';

import { SharedTreeService } from './shared/shared-tree.service';
import { StudentRe } from 'src/app/demo/_model/_Student/student';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { StudentService } from 'src/app/demo/Services/student.service';
import { ToastrService } from 'ngx-toastr';
import { error } from 'highcharts';
import { PayScreenShotRe } from 'src/app/demo/_model/payScreenShot';


@Component({
  selector: 'app-pendingStudent',
  templateUrl: './pendingStudent.component.html',
  styleUrls: ['./pendingStudent.component.scss']
})
export class PendingStudentComponent implements OnInit {
  RegisterForm: FormGroup;
  addStudentButton: string = "اضافة طالب"
  deleteStudentButton: string = "ازالة طالب"
  PayStatues: PayStatue[];
  PayStatueValue: number;
  imgSrc: string = "../../../../../assets/Logo.png"
  Students: StudentRe[];
  student: StudentRe;
  dtExportButtonOptions?: any = {};
  IsLoaded : boolean = false;

  StudentService: BaseService<StudentRe>;
  PayStatueService: BaseService<PayStatue>;
  constructor(
    public shared: SharedTreeService,
    private formBuilder: FormBuilder,
    private AccountService: AccountService,
    private studentSV: StudentService,
    private httpSv: HttpService,
    private toastr: ToastrService
  ) {

    this.StudentService = new BaseService<StudentRe>(httpSv, ApiPath.Student);
    this.PayStatueService = new BaseService<PayStatue>(httpSv, ApiPath.PayStatue);
  }

  ngOnInit() {
    this.shared.initForm();
    console.log(this.AccountService.user.appUserType.name_en);

    if (this.AccountService.user.appUserType.name_en == "Admin") {
      this.studentSV.GetPendingByAdminId().subscribe((res) => {
        this.Students = res;
        this.IsLoaded = true;
      });
    } if (this.AccountService.user.appUserType.name_en == "Manager") {
      this.studentSV.GetPendingByManagerId().subscribe((res) => {
        this.Students = res;
        this.IsLoaded = true;
      });
    } if (this.AccountService.user.appUserType.name_en == "Teacher") {
      this.studentSV.GetPendingByTeacherId().subscribe((res) => {
        this.Students = res;
        this.IsLoaded = true;
      });
    }
    this.PayStatueService.GetAll().subscribe(res => {
      this.PayStatues = res;
      this.IsLoaded = true;
    })
    this.dtExportButtonOptions = {
      dom: 'Bfrtip',
      buttons: ['copy','excel','csv','pdf','print']
    };
  }

  view(length : any, array:any ,model: any) {
   this.imgSrc = array[length].photoUrl
    model.show();
  }

  UpdateStatue(id: number, studentId: number) {


    this.studentSV.UpdatePayStatue(id, studentId).subscribe(res => {
      this.ngOnInit();
      this.toastr.success("تم تغيير الحالة بنجاح")
    }), error => {
      this.toastr.error("حدث خطا اثناء الحفظ")
    }

  }

  getCircleDetails(student: StudentRe, modal: any) {
    this.student = student;
    modal.show();
  }
  close(modal: any) {
    this.shared.clear()
    modal.hide();
  }
}
