import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { SharedTeacherService } from '../teacher/shared/shared-teacher.service';
import { TeacherRe } from 'src/app/demo/_model/_Teacher/teacher';
import { AccountService } from 'src/app/demo/Services/account/account.service';


@Component({
  selector: 'app-teacher',
  templateUrl: './teacher.component.html',
  styleUrls: ['./teacher.component.scss']
})
export class TeacherComponent implements OnInit {
  RegisterForm: FormGroup;
  addTeacherButton :string = "اضافة معلم"
  deleteTeacherButton :string = "ازالة معلم"

  teacherService: BaseService<TeacherRe>;
  teacher: TeacherRe;
  IsLoaded : boolean = false

  dtExportButtonOptions?: any = {};
  constructor(
    public shared: SharedTeacherService,
    private formBuilder: FormBuilder,
    private AccountService: AccountService,
    private httpSv: HttpService
  ) {
    this.teacherService = new BaseService<TeacherRe>(httpSv, ApiPath.Teacher);
  }

  ngOnInit() {
     this.shared.GetData();
    this.dtExportButtonOptions = {
      dom: 'Bfrtip',
      buttons: ['copy','excel','csv','pdf','print']
    };
    this.shared.initForm();

  }

  add(teacher: TeacherRe, model: any) {
    this.shared.initForm(teacher);
    this.shared.IsNew = true;
    model.show();
  }
  edit(teacher: TeacherRe, model: any) {
    this.shared.initForm(teacher);
    this.shared.IsNew = false;
    this.shared.btnLoader = false;
    model.show();
  }
  getCircleDetails(teacher: any, modal: any) {
    this.teacher = teacher;
    modal.show();
  }
  close(modal: any) {
    this.shared.clear()
    modal.hide();
  }
  
}


