import { PayStatue } from './../../../_model/payStatue';

import { GovernorateReDto } from '../../../_model/governorate';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { IOption } from 'ng-select';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { Nationality } from 'src/app/demo/_model/nationality';

import { SharedTreeService } from './shared/shared-tree.service';
import { StudentRe } from 'src/app/demo/_model/_Student/student';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { StudentService } from 'src/app/demo/Services/student.service';
import { ToastrService } from 'ngx-toastr';
import { error } from 'highcharts';
import { Subject } from 'rxjs';


@Component({
  selector: 'app-pendingRequest',
  templateUrl: './pendingRequest.component.html',
  styleUrls: ['./pendingRequest.component.scss']
})
export class PendingRequestComponent implements OnInit {

  PayStatues: PayStatue[];
  PayStatueValue: number;

  Students: StudentRe[];
  student: StudentRe;
  StudentService: BaseService<StudentRe>;
  dtExportButtonOptions?: any = {};
  IsLoaded: boolean = false;
  dtTrigger: Subject<any> = new Subject<any>();

  constructor(
    public shared: SharedTreeService,
    private formBuilder: FormBuilder,
    private AccountService: AccountService,
    private studentSV: StudentService,
    private httpSv: HttpService,
    private toastr: ToastrService
  ) {

    this.StudentService = new BaseService<StudentRe>(httpSv, ApiPath.Student);

  }

  ngOnInit() {
    this.dtExportButtonOptions = {
      dom: 'Bfrtip',
      buttons: ['copy', 'excel', 'csv', 'pdf', 'print']
    };


    if (this.AccountService.user.appUserType.name_en == "Admin") {
      this.studentSV.GetRequestByAdminId().subscribe((res) => {
        this.Students = res;
        this.IsLoaded = true;
       
        
      });}
       if (this.AccountService.user.appUserType.name_en == "Manager") {
      this.studentSV.GetRequestByManagerId().subscribe((res) => {
        this.Students = res;
        this.IsLoaded = true;

      });
    }
     if(this.AccountService.user.appUserType.name_en == "Teacher") {
      this.studentSV.GetPendingByTeacherId().subscribe((res) => {
        this.Students = res;
        this.IsLoaded = true;
      });
    }

  }



  Accept(studentId: number) {
    this.studentSV.AcceptStudent(studentId).subscribe(res => {
      this.ngOnInit();
      this.toastr.success("تم قبول الطالب  بنجاح")
    }), error => {
      this.toastr.error("حدث خطا اثناء الحفظ")
    }

  }


}
