import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';

import 'sweetalert2/src/sweetalert2.scss';
import Swal from 'sweetalert2/dist/sweetalert2.js';

import { TeacherService } from 'src/app/demo/Services/teacher.service';
import { TeacherAdd, TeacherRe } from 'src/app/demo/_model/_Teacher/teacher';
import { ToastrService } from 'ngx-toastr';
import { StudentAdd } from 'src/app/demo/_model/_Student/student';
import { UserPermissions } from 'src/app/demo/_model/userPermissions';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
@Injectable({
  providedIn: 'root'
})
export class SharedTeacherService {
  //prop
  selected: TeacherRe = { id: 0 } as TeacherRe;
  list: StudentAdd[] = [];
  TimeArray = [];
  managerTeachersArray = [];
  managerStudentsArray = [];
  StudentsTeachersArray = [];
  circleManagersArray = [];
  PermissionAray = [];
  IsNew: Boolean = true;
  teachers: TeacherRe[];
  teachersService: BaseService<TeacherRe>;
  IsLoaded: boolean = false
  public btnLoader: boolean;
  public submitLoader: boolean;
  //form
  form: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    private AccountService: AccountControllerService,
    private AccountsService: AccountService,
    private teacherService: TeacherService,
    private toastr: ToastrService,
    private httpSv: HttpService) { this.teachersService = new BaseService<TeacherRe>(httpSv, ApiPath.Teacher); }

  GetData() {
    if (this.AccountsService.user.appUserType.name_en == "Admin") {
      this.teachersService.GetAll().subscribe((res) => {
        this.teachers = res;
        this.IsLoaded = true;
      });
    }
    if (this.AccountsService.user.appUserType.name_en == "Manager") {
      this.teachersService.GetByManagerId().subscribe((res) => {
        this.teachers = res;
        this.IsLoaded = true
      });
    }
    if (this.AccountsService.user.appUserType.name_en == "Teacher") {
      this.teachersService.GetByTeacherId().subscribe((res) => {
        this.teachers = res;
        this.IsLoaded = true;
      });
    }
  }
  // init form
  initForm(data: TeacherRe = null) {

    this.selected = data;


    this.form = this.formBuilder.group({
      id: [data?.id | 0],
      firstName: [data?.firstName],
      lastName: [data?.lastName],
      email: [data?.email],
      governorateId: [data?.governorate?.id.toString()],
      forignTeacher: [data?.forignTeacher],
      mobileNumber: [data?.mobileNumber],
      teacherTimes: [data?.teacherTimes],
      managerTeachers: [data?.managerTeachers],
      teacherStudent: [data?.teacherStudents],
      teacherCircles: [data?.teacherCircles],
      userPermissions: [data?.userPermissions],
      password: [''],
      confirmPassword: ['']
    });
    data?.teacherTimes?.forEach((e) => {
      this.TimeArray.push(e?.id.toString());

    });
    this.form.get('teacherTimes').setValue(this.TimeArray);



    data?.teacherCircles?.forEach((e) => {
      this.circleManagersArray.push(e?.id.toString());
    });
    this.form.get('teacherCircles').setValue(this.circleManagersArray);



    data?.managerTeachers?.forEach((e) => {
      this.managerTeachersArray.push(e?.id.toString());
    });
    this.form.get('managerTeachers').setValue(this.managerTeachersArray);

    data?.userPermissions?.forEach((e) => {
      this.PermissionAray.push(e.toString());
      console.log(this.PermissionAray);

    });
    this.form.get('userPermissions').setValue(this.PermissionAray);


    data?.teacherStudents?.forEach((e) => {
      this.managerStudentsArray.push(e?.id.toString());
    });
    this.form.get('teacherStudent').setValue(this.managerStudentsArray);


  }

  add(modal: any) {
    this.btnLoader = true;
    console.log(this.form.value);
    if (this.form.valid) {

      let data: TeacherAdd = this.form.value;
      if (data.forignTeacher == null) {
        data.forignTeacher = false;
      }
      console.log(data);

      data.teacherTimes = [];
      data.managerTeachers = [];
      data.teacherStudents = [];
      data.teacherCircles = [];
      this.form.controls?.teacherTimes?.value.forEach((e) => {
        let time = {
          id: 0,
          timeId: e,
          teacherId: 0,
          isDeleted: false
        };
        data.teacherTimes.push(time);
      });

      this.form.controls?.managerTeachers?.value.forEach((e) => {
        let teachers = {
          id: 0,
          teacherId: 0,
          managerId: e,
          isDeleted: false
        };
        data.managerTeachers.push(teachers);
      });
      this.form.controls?.teacherStudent?.value.forEach((e) => {
        let managerStudents = {
          id: 0,
          studentId: e,
          teacherId: 0,
          isDeleted: false
        };
        data.teacherStudents?.push(managerStudents);
      });
      this.form.controls?.teacherCircles?.value.forEach((e) => {
        let circleManagers = {
          id: 0,
          circleId: e,
          teacherId: 0,
          isDeleted: false

        };
        data.teacherCircles?.push(circleManagers);
      });

      this.AccountService.AddTeacher(data).subscribe((res) => {
        this.toastr.success("تم اضافة المعلم بنجاح")
        this.teachers.push(res);
        modal.hide();
        this.btnLoader = false;
        this.form.reset();

      }),
        (err) => {
          this.toastr.error("تحقق من جودة الاتصال بالانترنت")
          this.btnLoader = false;
        };
    }
  }
  Update(modal: any): void {
    this.btnLoader = true;
    let data: TeacherAdd = this.form.value;
    data.teacherTimes = [];
    data.managerTeachers = [];
    data.teacherStudents = [];
    data.teacherCircles = [];
    this.form.controls?.teacherTimes?.value.forEach((e) => {
      let time = {
        id: 0,
        timeId: e,
        teacherId: 0,
        isDeleted: false
      };
      data.teacherTimes.push(time);
    });

    this.form.controls?.managerTeachers?.value.forEach((e) => {
      let teachers = {
        id: 0,
        teacherId: 0,
        managerId: e,
        isDeleted: false
      };
      data.managerTeachers.push(teachers);
    });
    this.form.controls?.teacherStudent?.value.forEach((e) => {
      let managerStudents = {
        id: 0,
        studentId: e,
        teacherId: 0,
        isDeleted: false
      };
      data.teacherStudents?.push(managerStudents);
    });
    this.form.controls?.teacherCircles?.value.forEach((e) => {
      let circleManagers = {
        id: 0,
        circleId: e,
        teacherId: 0,
        isDeleted: false

      };
      data.teacherCircles?.push(circleManagers);
    });

    this.teacherService.Update(data).subscribe((res) => {
      let index = this.teachers.findIndex((x) => x.id === data.id);
      this.teachers[index] = res;

      this.toastr.success("تم تحديث بيانات المعلم بنجاح")
      modal.hide();
      this.btnLoader = false;
    },
      (err) => {
        this.toastr.error("تحقق من جودة الاتصال بالانترنت")
        this.btnLoader = false;
      });
  }
  clear() {
    this.selected = { id: 0 } as TeacherRe;
    this.list = [];
    this.TimeArray = [];
    this.managerTeachersArray = [];
    this.managerStudentsArray = [];
    this.StudentsTeachersArray = [];
  }

  confirmAlert(teacher: any) {
    Swal.fire({
      title: 'هل انت متاكد من حذف المعلم',
      text: 'اذا تم الحذف لن تكون قادرا علي الاسترجاع مرة اخرى',
      type: 'warning',
      showCloseButton: true,
      showCancelButton: true
    }).then((willDelete) => {
      if (willDelete.dismiss) {
        Swal.fire('', 'لم يتم الحذف', 'error');

      } else {
        this.teacherService.Delete(teacher.id).subscribe(res => {
          Swal.fire('', 'تم حذف المعلم', 'success');
          let index = this.teachers.indexOf(teacher)
          this.teachers.splice(index, 1);
          this.toastr.success("تم حذف المعلم بنجاح")
        });
      }
    });
  }

}
