import { Injectable } from '@angular/core';
import { HttpService } from './http/http.service';

@Injectable({
  providedIn: 'root'
})
export class TeacherService {
  controller = "Teacher/";

  constructor(private http:HttpService) { }

  Update(model: any) {
    return this.http.HttpPost(this.controller  + 'update/', model);
  }
  Delete(id: any) {
    return this.http.httpDelete(this.controller  + id);
  }


}
