
import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';

import 'sweetalert2/src/sweetalert2.scss';
import Swal from 'sweetalert2/dist/sweetalert2.js';

import { TeacherService } from 'src/app/demo/Services/teacher.service';

import { ToastrService } from 'ngx-toastr';


import { CircleService } from 'src/app/demo/Services/circle/circle.service';

import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { Quran } from 'src/app/demo/_model/quran';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { StudentReportRe } from 'src/app/demo/_model/StudentReport';
import { CircleAdd, CircleRe } from 'src/app/demo/_model/_Circle/circle';


@Injectable({
  providedIn: 'root'
})
export class SharedCircleService {
  //prop
  selected: CircleRe = { id: 0 } as CircleRe;
  reportSelected: StudentReportRe = { id: 0 } as StudentReportRe;
  list: CircleAdd[] = [];
  TimeArray = [];
  circleManagersArray = [];
  circleTimesArray = [];
  circleStudentsArray = [];
  teacherCirclesArray = [];
  IsNew: Boolean = true;
  quranService: BaseService<Quran>;
  StudentReportService: BaseService<StudentReportRe>;

  //form
  form: FormGroup;
  reportForm: FormGroup;

  constructor(
    private httpSv: HttpService,
    private formBuilder: FormBuilder,
    private CircleService: CircleService,
    private teacherService: TeacherService,
    private toastr: ToastrService,
  ) {
    this.quranService = new BaseService<Quran>(httpSv, ApiPath.Quran);
    this.StudentReportService = new BaseService<StudentReportRe>(httpSv, ApiPath.StudentReport);
  }

  initReportForm(data: StudentReportRe = null) {
    this.reportSelected = data;
    this.reportForm = this.formBuilder.group({
      id: [data?.id | 0],
      newId: [data?.newId],
      minutes: [data?.minutes| 0],
      newFrom: [data?.newFrom| 0],
      newTo: [data?.newTo| 0],
      newRate: [data?.newRate ],
      recentPastId: [data?.recentPastId],
      recentPastFrom: [data?.recentPastFrom| 0],
      recentPastTo: [data?.recentPastTo| 0],
      recentPastRate: [data?.recentPastRate],
      distantPastId: [data?.distantPastId],
      distantPastFrom: [data?.distantPastFrom| 0],
      distantPastTo: [data?.distantPastTo| 0],
      distantPastRate: [data?.distantPastRate],
      farthestPastId: [data?.farthestPastId],
      farthestPastFrom: [data?.farthestPastFrom| 0],
      farthestPastTo: [data?.farthestPastTo| 0],
      farthestPastRate: [data?.farthestPastRate],
      theWordsQuranStranger: [data?.theWordsQuranStranger],
      intonation: [data?.intonation],
      other: [data?.other],
      circleId: [data?.circleId],
      studentId: [data?.studentId],
      teacherId: [data?.teacherId],
      attendStatueId: [data?.attendStatueId],

    });
  }

  // init form
  initForm(data: CircleRe = null) {
    this.selected = data;
    this.form = this.formBuilder.group({
      id: [data?.id | 0],
      name: [data?.name],
      circleTimes: [data?.circleTimes],
      circleManagers: [data?.circleManagers],
      circleStudents: [data?.circleStudents],
      teacherCircles: [data?.teacherCircles],
    });
    data?.circleTimes?.forEach((e) => {
      this.circleTimesArray.push(e?.timeId.toString());

    });
    this.form.get('circleTimes').setValue(this.circleTimesArray);


    data?.circleManagers?.forEach((e) => {
      this.circleManagersArray.push(e?.id.toString());

    });
    this.form.get('circleManagers').setValue(this.circleManagersArray);



    data?.circleStudents?.forEach((e) => {
      this.circleStudentsArray.push(e?.id.toString());
    });
    this.form.get('circleStudents').setValue(this.circleStudentsArray);



    data?.teacherCircles?.forEach((e) => {
      this.teacherCirclesArray.push(e?.id.toString());
    });
    this.form.get('teacherCircles').setValue(this.teacherCirclesArray);
  }

  add(modal: any) {
    if (this.form.valid) {
      let data: CircleAdd = this.form.value;
      data.circleStudents = [];
      data.teacherCircles = [];
      data.circleManagers = [];
      data.circleTimes = [];


      this.form.controls?.circleTimes?.value.forEach((e) => {
        let circle = {
          id: 0,
          circleId: 0,
          timeId: e,
          isDeleted: false
        };
        data.circleTimes.push(circle);
      });


      this.form.controls?.teacherCircles?.value.forEach((e) => {
        let circle = {
          id: 0,
          circleId: 0,
          teacherId: e,
          isDeleted: false
        };
        data.teacherCircles.push(circle);
      });

      this.form.controls?.circleStudents?.value.forEach((e) => {
        let student = {
          id: 0,
          studentId: e,
          circleId: 0,
          isDeleted: false
        };
        data.circleStudents.push(student);
      });
      this.form.controls?.circleManagers?.value.forEach((e) => {
        let circleManagers = {
          id: 0,
          managerId: e,
          circleId: 0,
          isDeleted: false
        };
        data.circleManagers?.push(circleManagers);
      });

      this.CircleService.Add(data).subscribe((res) => {
        modal.hide();
        this.form.reset();
        this.toastr.success("تم اضافة الحلقة بنجاح")
      }),
        (err) => {
          console.log(err);
        };
    }
  }




  CreateReport(modal: any, studentId: number, circleId: number) {
    if (this.reportForm.valid) {
      let data: StudentReportRe = this.reportForm.value;
      data.studentId = studentId;
      data.circleId = circleId;
      this.StudentReportService.Add(data).subscribe((res) => {
        modal.hide();
        this.form.reset();
        this.toastr.success("تم اضافة التقرير بنجاح")
      }),
        (err) => {
          console.log(err);
        };
    }
  }
  Update(modal: any): void {
    let data: CircleAdd = this.form.value;
    data.circleStudents = [];
    data.teacherCircles = [];
    data.circleManagers = [];
    data.circleTimes = [];


    this.form.controls?.circleTimes?.value.forEach((e) => {
      let circle = {
        id: 0,
        circleId: 0,
        timeId: e,
        isDeleted: false
      };
      data.circleTimes.push(circle);
    });
    this.form.controls?.teacherCircles?.value.forEach((e) => {
      let circle = {
        id: 0,
        circleId: 0,
        teacherId: e,
        isDeleted: false
      };
      data.teacherCircles.push(circle);
    });

    this.form.controls?.circleStudents?.value.forEach((e) => {
      let student = {
        id: 0,
        studentId: e,
        circleId: 0,
        isDeleted: false
      };
      data.circleStudents.push(student);
    });
    this.form.controls?.circleManagers?.value.forEach((e) => {
      let circleManagers = {
        id: 0,
        managerId: e,
        circleId: 0,
        isDeleted: false
      };
      data.circleManagers?.push(circleManagers);
    });


    this.CircleService.Update(data).subscribe((res) => {
      modal.hide();
      window.location.reload();
    });
  }
  clear() {
    this.selected = { id: 0 } as CircleRe;
    this.list = [];
    this.TimeArray = [];

  }

  confirmAlert(id: number) {
    Swal.fire({
      title: 'هل انت متاكد من حذف الحلقة',
      text: 'اذا تم الحذف لن تكون قادرا علي الاسترجاع مرة اخرى',
      type: 'warning',
      showCloseButton: true,
      showCancelButton: true
    }).then((willDelete) => {
      if (willDelete.dismiss) {
        Swal.fire('', 'لم يتم الحذف', 'error');

      } else {
        this.CircleService.Delete(id).subscribe(res => {
          console.log(res);
          Swal.fire('', 'تم حذف الحلقة', 'success');
          window.location.reload();
        });
      }
    });
  }

}
