import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    children: [
      {
        path: 'dashboard',
        loadChildren: () => import('./hosp-dashboard/dashboard.module').then((module) => module.DashboardModule)
      },
      {
        path: 'incomingAndOutgoing',
        loadChildren: () => import('./incomingAndOutgoing/incomingAndOutgoing.module').then((module) => module.IncomingAndOutgoingModule)
      },
      {
        path: 'hosp-department',
        loadChildren: () => import('./hosp-department/hosp-department.module').then((module) => module.HospDepartmentModule)
      },
      {
        path: 'manager',
        loadChildren: () => import('./manager/manager.module').then((module) => module.ManagerModule)
      },
      {
        path: 'family',
        loadChildren: () => import('./family/family.module').then((module) => module.FamilyModule)
      },
      {
        path: 'teacher',
        loadChildren: () => import('./teacher/teacher.module').then((module) => module.TeacherModule)
      },
      {
        path: 'student',
        loadChildren: () => import('./student/student.module').then((module) => module.StudentModule)
      },
      {
        path: 'pendingStudent',
        loadChildren: () => import('./pendingStudent/pendingStudent.module').then((module) => module.PendingStudentModule)
      },
      {
        path: 'pendingRequest',
        loadChildren: () => import('./pendingRequest/pendingRequest.module').then((module) => module.PendingRequestModule)
      },
      {
        path: 'circle',
        loadChildren: () => import('./circle/circle.module').then((module) => module.CircleModule)
      },
      {
        path: 'studentReports',
        loadChildren: () => import('./studentReports/studentReports.module').then((module) => module.StudentReportsModule)
      }
      ,
      {
        path: 'trash',
        loadChildren: () => import('./trash/trash.module').then((module) => module.TrashModule)
      },
      {
        path: 'deletedCircle',
        loadChildren: () => import('./deletedCircle/deletedCircle.module').then((module) => module.DeletedCircleModule)
      },
      {
        path: 'deletedManager',
        loadChildren: () => import('./deletedManager/deletedManager.module').then((module) => module.DeletedManagerModule)
      },
      {
        path: 'deletedTeacher',
        loadChildren: () => import('./deletedTeacher/deletedTeacher.module').then((module) => module.DeletedTeacherModule)
      },
      {
        path: 'deletedFamily',
        loadChildren: () => import('./deletedFamily/deletedFamily.module').then((module) => module.DeletedFamilyModule)
      }
      
      
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HospitalRoutingModule { }
