
import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';

import 'sweetalert2/src/sweetalert2.scss';
import Swal from 'sweetalert2/dist/sweetalert2.js';

import { TeacherService } from 'src/app/demo/Services/teacher.service';

import { ToastrService } from 'ngx-toastr';


import { CircleService } from 'src/app/demo/Services/circle/circle.service';

import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { Quran } from 'src/app/demo/_model/quran';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { StudentReportRe } from 'src/app/demo/_model/StudentReport';
import { CircleAdd, CircleRe } from 'src/app/demo/_model/_Circle/circle';
import { StudentRe } from 'src/app/demo/_model/_Student/student';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { StudentReportService } from 'src/app/demo/Services/studentReport/studentReport.service';

@Injectable({
  providedIn: 'root'
})
export class SharedStudentReportsService {
  //prop
  selected: CircleRe = { id: 0 } as CircleRe;
  reportSelected: StudentReportRe = { id: 0 } as StudentReportRe;
  list: CircleAdd[] = [];
  TimeArray = [];
  circleManagersArray = [];
  circleTimesArray = [];
  circleStudentsArray = [];
  teacherCirclesArray = [];
  IsNew: Boolean = true;
  quranService: BaseService<Quran>;
  StudentService: BaseService<StudentRe>;
  Students: StudentRe;
  Reports: StudentReportRe;
  IsLoaded: boolean = false;

  //form
  form: FormGroup;
  reportForm: FormGroup;

  constructor(
    private httpSv: HttpService,
    private formBuilder: FormBuilder,
    private CircleService: CircleService,
    private teacherService: TeacherService,
    private AccountService: AccountService,
    private toastr: ToastrService,
    private StudentReportService : StudentReportService
  ) {
    this.quranService = new BaseService<Quran>(httpSv, ApiPath.Quran);
    
    this.StudentService = new BaseService<StudentRe>(httpSv, ApiPath.Student);
  }

  initReportForm(data: StudentReportRe = null) {
    this.reportSelected = data;
    this.reportForm = this.formBuilder.group({
      id: [data?.id | 0],
      newId: [data?.newId],
      minutes: [data?.minutes | 0],
      newFrom: [data?.newFrom | 0],
      newTo: [data?.newTo | 0],
      newRate: [data?.newRate],
      recentPastId: [data?.recentPastId?.toString()],
      recentPastFrom: [data?.recentPastFrom | 0],
      recentPastTo: [data?.recentPastTo | 0],
      recentPastRate: [data?.recentPastRate],
      distantPastId: [data?.distantPastId?.toString()],
      distantPastFrom: [data?.distantPastFrom | 0],
      distantPastTo: [data?.distantPastTo | 0],
      distantPastRate: [data?.distantPastRate],
      farthestPastId: [data?.farthestPastId?.toString()],
      farthestPastFrom: [data?.farthestPastFrom | 0],
      farthestPastTo: [data?.farthestPastTo | 0],
      farthestPastRate: [data?.farthestPastRate],
      theWordsQuranStranger: [data?.theWordsQuranStranger],
      intonation: [data?.intonation],
      other: [data?.other],
      circleId: [data?.circleId?.toString()],
      studentId: [data?.studentId],
      teacherId: [data?.teacherId],
      attendStatueId: [data?.attendStatueId.toString()],
    });
  }

  // init form

  UpdateReport(modal: any,modal2: any) {
    if (this.reportForm.valid) {
      let data: StudentReportRe = this.reportForm.value;
      this.StudentReportService.Update(data).subscribe((res) => {
        modal.hide();
        modal2.hide();
        this.form.reset();
        this.toastr.success("تم تعديل  التقرير بنجاح")
      }),
        (err) => {
          console.log(err);
        };
    }
  }

  GetStudents() {
    if (this.AccountService.user.appUserType.name_en == "Admin") {
      this.StudentService.GetAll().subscribe((res) => {
        this.Students = res;
        this.IsLoaded = true;

      });
    } if (this.AccountService.user.appUserType.name_en == "Manager") {
      this.StudentService.GetByManagerId().subscribe((res) => {
        this.Students = res;
        this.IsLoaded = true;

      });
    } if (this.AccountService.user.appUserType.name_en == "Teacher") {
      this.StudentService.GetByTeacherId().subscribe(res => {
        this.Students = res;
        this.IsLoaded = true;

      });
    }
  }

  GetReports(studentId: any, modal: any) {
    this.StudentReportService.GetByStudentId(studentId).subscribe(res => {
      this.Reports = res;
      console.log(this.Reports);
    })
    modal.show();
  }
}
