import { TimeRe } from 'src/app/demo/_model/timeRe';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { IOption } from 'ng-select';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { StudentService } from 'src/app/demo/Services/student.service';
import { ManagerRe } from 'src/app/demo/_model/_Manager/manager';
import { TeacherRe } from 'src/app/demo/_model/_Teacher/teacher';
import { StudentRe } from 'src/app/demo/_model/_Student/student';
import { SharedStudentReportsService } from '../shared/shared-StudentReports.service';
import { CircleRe } from 'src/app/demo/_model/_Circle/circle';
import { Quran } from 'src/app/demo/_model/quran';
import { AttendStatue } from 'src/app/demo/_model/attendStatue';

@Component({
    selector: 'app-editReport',
    templateUrl: './editReport.component.html',
    styleUrls: ['./editReport.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class EditReportComponent implements OnInit {
    Qurans: Array<IOption>;
    AttendStatues: Array<IOption>;
    circleService: BaseService<CircleRe>;
    teacherService: BaseService<TeacherRe>;
    quranService: BaseService<Quran>;
    attendStatueService: BaseService<AttendStatue>;


    constructor(

        public shared: SharedStudentReportsService,
        private formBuilder: FormBuilder,
        private AccountService: AccountControllerService,
        private httpSv: HttpService,
        private studentService:StudentService

    ) {

        this.circleService = new BaseService<CircleRe>(httpSv, ApiPath.Circle);
        this.teacherService = new BaseService<TeacherRe>(httpSv, ApiPath.Teacher);
        this.quranService = new BaseService<Quran>(httpSv, ApiPath.Quran);
        this.attendStatueService = new BaseService<AttendStatue>(httpSv, ApiPath.AttendStatue);
    }
    ngOnInit() {
      this.quranService.GetAll().subscribe((res: Quran[]) => {
        const data = res.map((x) => {
            const temp = {
                label: x.name,
                value: x.id.toString()
            };
            return temp;
        });
        this.Qurans = data;

    });
    this.attendStatueService.GetAll().subscribe((res: AttendStatue[]) => {
        const data = res.map((x) => {
            const temp = {
                label: x.name,
                value: x.id.toString()
            };
            return temp;
        });
        this.AttendStatues = data;
        console.log(this.AttendStatues);
        

    });
        
    }


}
