import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';

import 'sweetalert2/src/sweetalert2.scss';
import Swal from 'sweetalert2/dist/sweetalert2.js';


import { ToastrService } from 'ngx-toastr';
import { StudentAdd } from 'src/app/demo/_model/_Student/student';
import { UserPermissions } from 'src/app/demo/_model/userPermissions';
import { FamilyAdd, FamilyRe } from 'src/app/demo/_model/family';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { HttpService } from 'src/app/demo/Services/http/http.service';
@Injectable({
  providedIn: 'root'
})
export class SharedFamilyService {
  //prop
  selected: FamilyRe = { id: 0 } as FamilyRe;
  list: StudentAdd[] = [];
  students = [];
  managerfamilysArray = [];
  managerStudentsArray = [];
  StudentsfamilysArray = [];
  circleManagersArray = [];
  PermissionAray = [];
  IsNew: Boolean = true;
  public btnLoader: boolean;
  public submitLoader: boolean;
  //form
  form: FormGroup;
  familyService: BaseService<FamilyAdd>;
  constructor(
    private formBuilder: FormBuilder,
    private AccountService: AccountControllerService,
    private toastr: ToastrService,
    private httpSv: HttpService
  ) { this.familyService = new BaseService<FamilyAdd>(httpSv, ApiPath.Family);}

  // init form
  initForm(data: FamilyRe = null) {

    this.selected = data;

    this.form = this.formBuilder.group({
      id: [data?.id | 0],
      name: [data?.name],
      familyStudents: [data?.familyStudents],

    });
    data?.familyStudents?.forEach((e) => {
      this.students.push(e?.id.toString());
    });
    this.form.get('familyStudents').setValue(this.students);

  }

  add(modal: any) {
    this.btnLoader = true;
    if (this.form.valid) {
      let data: FamilyAdd = this.form.value;
      data.familyStudents = [];
      this.form.controls?.familyStudents?.value.forEach((e) => {
        let time = {
          studentId :e,
        };
        data.familyStudents.push(time);
      });


      this.familyService.Add(data).subscribe((res) => {
        this.toastr.success("تم اضافة العائلة بنجاح")
        modal.hide();
        this.form.reset();
        this.btnLoader = false;
        // window.location.reload();
      }),
        (err) => {
          this.toastr.error(err)
        };
    }
  }
  Update(modal: any): void {
    let data: FamilyAdd = this.form.value;
    data.familyStudents = [];
    this.form.controls?.familyStudents?.value.forEach((e) => {
      let time = {
        studentId :e,
      };
      data.familyStudents.push(time);
    });

    this.familyService.Update(data).subscribe((res) => {
      modal.hide();
      window.location.reload();
    });
  }


  confirmAlert(id: number) {
    Swal.fire({
      title: 'هل انت متاكد من حذف العائلة',
      text: 'اذا تم الحذف لن تكون قادرا علي الاسترجاع مرة اخرى',
      type: 'warning',
      showCloseButton: true,
      showCancelButton: true
    }).then((willDelete) => {
      if (willDelete.dismiss) {
        Swal.fire('', 'لم يتم الحذف', 'error');

      } else {
        this.familyService.Delete(id).subscribe(res => {
          console.log(res);
          Swal.fire('', 'تم حذف العائلة', 'success');
          window.location.reload();
        });
      }
    });
  }

}
