import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { SharedFamilyService } from '../family/shared/shared-family.service';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { FamilyRe } from 'src/app/demo/_model/family';
import { IOption } from 'ng-select';
import { StudentRe } from 'src/app/demo/_model/_Student/student';
import { GovernorateReDto } from 'src/app/demo/_model/governorate';
import { Nationality } from 'src/app/demo/_model/nationality';
import { SharedTreeService } from '../student/shared/shared-tree.service';
import { SharedTrashService } from './shared/shared-trash.service';


@Component({
  selector: 'app-trash',
  templateUrl: './trash.component.html',
  styleUrls: ['./trash.component.scss']
})
export class TrashComponent implements OnInit {
  RegisterForm: FormGroup;
  addStudentButton: string = "اضافة طالب"
  deleteStudentButton: string = "ازالة طالب"
  Nationalities: Array<IOption>;
  Governorates: Array<IOption>;
  GovernorateId: number;
  StudentService: BaseService<StudentRe>;
  student: StudentRe;
  Students: StudentRe[];
  IsLoaded: boolean = false;
  NationalityId = '1';
  NationalityService: BaseService<Nationality>;
  GovernorateService: BaseService<GovernorateReDto>;

  dtExportButtonOptions?: any = {};
  constructor(
    public shared: SharedTrashService,
    private formBuilder: FormBuilder,
    private AccountService: AccountService,
    private httpSv: HttpService
  ) {
    this.NationalityService = new BaseService<Nationality>(httpSv, ApiPath.Nationality);
    this.GovernorateService = new BaseService<GovernorateReDto>(httpSv, ApiPath.Governorate);
    this.StudentService = new BaseService<StudentRe>(httpSv, ApiPath.Student);
  }

  ngOnInit() {
    this.shared.GetData();
    this.dtExportButtonOptions = {
      dom: 'Bfrtip',
      buttons: ['copy','excel','csv','pdf','print']
    };
  }

  

  getCircleDetails(student: StudentRe, modal: any) {
    this.student = student;
    modal.show();
  }
  close(modal: any) {
    modal.hide();
  }
}
