import { StudentService } from '../../../../Services/student.service';
import { CircleStudentAdd } from './../../../../_model/circleStudent';

import { Injectable } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AccountControllerService } from 'src/app/demo/Services/AccountController.service';
import 'sweetalert2/src/sweetalert2.scss';
import Swal from 'sweetalert2/dist/sweetalert2.js';
import { StudentAdd, StudentRe } from 'src/app/demo/_model/_Student/student';
import { AccountService } from 'src/app/demo/Services/account/account.service';
import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ToastService } from 'src/app/theme/shared/components/toast/toast.service';
import { ToastrService } from 'ngx-toastr';
import { PayScreenShotAdd } from 'src/app/demo/_model/payScreenShot';
@Injectable({
  providedIn: 'root'
})
export class SharedTreeService {
  //prop
  selected: StudentRe = { id: 0 } as StudentRe;
  list: StudentRe[] = [];
  circleArray = [];
  timeArray = [];
  managerArray = [];
  teacherArray = [];
  StudentService: BaseService<StudentRe>;
  student: StudentRe;
  Students: StudentRe[];
  IsLoaded: boolean = false;
  public btnLoader: boolean;
  public submitLoader: boolean;
  imageForm: FormGroup;
  PayService: BaseService<PayScreenShotAdd>;
  //form
  form: FormGroup;

  constructor(
    private formBuilder: FormBuilder,
    private AccountService: AccountControllerService,
    private studentService: StudentService,
    private AccountsService : AccountService,
    private httpSv: HttpService,
    private toastr : ToastrService

  ) {  this.StudentService = new BaseService<StudentRe>(httpSv, ApiPath.Student);
    this.PayService = new BaseService<PayScreenShotAdd>(httpSv, ApiPath.PayScreenShot);}

  GetData(){
    if (this.AccountsService.user.appUserType.name_en == "Admin") {
      this.StudentService.GetAll().subscribe((res) => {
        this.Students = res;
        console.log(res);
        this.IsLoaded = true;
      });
    }
    if (this.AccountsService.user.appUserType.name_en == "Manager") {
      this.StudentService.GetByManagerId().subscribe((res) => {
        this.Students = res;
        console.log(res);
        
        this.IsLoaded = true;
      });
    }
    if (this.AccountsService.user.appUserType.name_en == "Teacher"){
      this.StudentService.GetByTeacherId().subscribe((res) => {
        this.Students = res;
        console.log(res);
        this.IsLoaded = true;
      });
    }
  }
  // init form
  initForm(data: StudentRe = null) {

    this.selected = data;


    this.form = this.formBuilder.group({
      id: [data?.id | 0],
      firstName: [data?.firstName],
      lastName: [data?.lastName],
      email: [data?.email],
      age: [data?.age],
      freeCircle: [data?.freeCircle],
      mobileNumber: [data?.mobileNumber],
      nationalityId: [data?.nationality?.id.toString()],
      governorateId: [data?.governorate?.id.toString()],
      familyId: [data?.family?.id.toString()],

      payStatueId: [data?.payStatue?.id.toString()],
      subscribeId: [data?.subscribe?.id.toString()],
      studentTimes: [data?.studentTimes],
      circleStudents: [data?.circleStudents],
      teacherStudents: [data?.teacherStudents],
      managerStudents: [data?.managerStudents],
      savedParts: [data?.savedParts],
      password: [''],
      confirmPassword: ['']
    });

    data?.circleStudents?.forEach((e) => {
      this.circleArray.push(e?.id.toString());
    });
    this.form.get('circleStudents').setValue(this.circleArray);

    data?.teacherStudents?.forEach((e) => {
      this.teacherArray.push(e?.id.toString());
    });
    this.form.get('teacherStudents').setValue(this.teacherArray);

    data?.managerStudents?.forEach((e) => {
      this.managerArray.push(e?.id.toString());
    });
    this.form.get('managerStudents').setValue(this.managerArray);

    data?.studentTimes?.forEach((e) => {
      this.timeArray.push(e?.id.toString());
      console.log(this.timeArray);
    });
    this.form.get('studentTimes').setValue(this.timeArray);
  }
  

  add(modal: any) {
    this.btnLoader = true;
    if (this.form.valid) {
      let data: StudentAdd = this.form.value;
      if (data.freeCircle == null) {
        data.freeCircle = false;
      }
      console.log(data);

      data.studentTimes = [];
      this.form.controls?.studentTimes?.value.forEach((e) => {
        let time = {
          id: 0,
          timeId: e,
          studentId: 0,
          isDeleted: false
        };
        data.studentTimes.push(time);
      });
      data.circleStudents = [];
      this.form.controls?.circleStudents?.value.forEach((e) => {
        let circle = {
          id: 0,
          circleId: e,
          studentId: 0,
          isDeleted: false
        };
        data.circleStudents.push(circle);
      });
      data.teacherStudents = [];
      this.form.controls?.teacherStudents?.value.forEach((e) => {
        let teacher = {
          id: 0,
          teacherId: e,
          studentId: 0,
          isDeleted: false
        };
        data.teacherStudents.push(teacher);
      });
      data.managerStudents = [];
      this.form.controls?.managerStudents?.value.forEach((e) => {
        let manager = {
          id: 0,
          managerId: e,
          studentId: 0,
          isDeleted: false
        };
        data.managerStudents.push(manager);
      });
      this.AccountService.AddByAdmin(data).subscribe((res) => {
        modal.hide();
        this.Students.push(res);
        this.form.reset();
        this.btnLoader = false;
        this.toastr.success("تم اضافة الطالب بنجاح")
      }),
        (err) => {
          this.toastr.error("تحقق من جودة الاتصال بالانترنت")
          this.btnLoader = false;
        };
    }else{
      this.btnLoader = false;
    }
  }
  Update(modal: any): void {
    this.btnLoader = true;
    let data: StudentAdd = this.form.value;
    data.studentTimes = [];
    this.form.controls?.studentTimes?.value.forEach((e) => {
      let time = {
        id: 0,
        timeId: e,
        studentId: 0,
        isDeleted: false
      };
      data.studentTimes.push(time);
    });
    data.circleStudents = [];
    this.form.controls?.circleStudents?.value.forEach((e) => {
      let circle = {
        id: 0,
        circleId: e,
        studentId: 0,
        isDeleted: false
      };
      data.circleStudents.push(circle);
    });
    data.teacherStudents = [];
    this.form.controls?.teacherStudents?.value.forEach((e) => {
      let teacher = {
        id: 0,
        teacherId: e,
        studentId: 0,
        isDeleted: false
      };
      data.teacherStudents.push(teacher);
    });
    data.managerStudents = [];
    this.form.controls?.managerStudents?.value.forEach((e) => {
      let manager = {
        id: 0,
        managerId: e,
        studentId: 0,
        isDeleted: false
      };
      data.managerStudents.push(manager);
    });

    this.studentService.Update(data).subscribe((res) => {
      modal.hide();
      this.AddPayScreen(data.id)
      let index = this.Students.findIndex((x) => x.id === data.id);
      this.Students[index] = res;
      this.btnLoader = false;
      this.toastr.success("تم تحديث بيانات الطالب بنجاح")
    }),
    (err) => {
      this.toastr.error("تحقق من جودة الاتصال بالانترنت")
      this.btnLoader = false;
      
    };
  }
  clear(modal:any) {
    this.selected = { id: 0 } as StudentRe;
    this.list = [];
    this.circleArray = [];
    this.timeArray = [];
    modal.hide();
    this.form.reset();
  }

  confirmAlert(Student: any) {
    Swal.fire({
      title: 'هل انت متاكد من حذف الطالب',
      text: 'اذا تم الحذف لن تكون قادرا علي الاسترجاع مرة اخرى',
      type: 'warning',
      showCloseButton: true,
      showCancelButton: true
    }).then((willDelete) => {
      if (willDelete.dismiss) {
        Swal.fire('', 'لم يتم الحذف', 'error');

      } else {
        this.studentService.Delete(Student.id).subscribe(res => {
          let index = this.Students.indexOf(Student)
          this.Students.splice(index, 1);
          Swal.fire('', 'تم حذف الطالب', 'success');
          this.toastr.success("تم حذف الطالب بنجاح")
          Swal.fire('', 'تم حذف الطالب', 'success');
        });
      }
    });
  }
  AddPayScreen(studentId:number ){
    let data: PayScreenShotAdd = this.imageForm.value;
    data.studentId = studentId;
    this.PayService.Add(data).subscribe((res) => {
    });
  }
  initImageForm() {
    this.imageForm = this.formBuilder.group({
      id:[0],
      fileBase64: ['', Validators.required],
    });
}
  AddPhoto(inputValue: FileList) {
    let file = inputValue.item(0);
    let data: FormData = new FormData();

    data.append('file', file);

    let reader: FileReader = new FileReader();

    reader.onloadend = this._handleReaderLoaded.bind(this, );
    reader.readAsBinaryString(file);
  }
  _handleReaderLoaded(readerEvt) {
    var binaryString = readerEvt.target.result;

    let imageData = btoa(binaryString);

    this.imageForm.controls["fileBase64"].setValue(imageData);
  }
}
